					})
				}
			},
			getLocationsss(isDaka) {
				var that = this
				console.log('getLocationsss')
				myAmapFun.getRegeo({
					success: function(data) {
						// console.log(data[0]);
						that.curAdsMsg = {
							"place": data[0].name,
							'lat': data[0].latitude,
							'lng': data[0].longitude
						};
						
						console.log(that.curAdsMsg)
						if(isDaka){
							that.requestDaka()
						}
					},
					fail: function(info) {
						//失败回调
						console.log('失败回调' + JSON.stringify(info))
						if(isDaka){
							that.isSHowDaka = true
							that.dakaRes = false
						}
					}
					
				})
			
			
			},
			requestDaka() {
				this.dakaRes = true
				this.isSHowDaka = true
			},
			hideDaka(){
				this.isSHowDaka = false
			},
		}
	}
